useEffect(() => {
  const unsubscribe = auth.onAuthStateChanged(async (authUser) => {
    if (authUser) {
      setUser(authUser);

      const userDocRef = doc(firestore, 'expertApplications', authUser.uid);
      const userDocSnapshot = await getDoc(userDocRef);

      if (userDocSnapshot.exists() && userDocSnapshot.data().approved) {
        // User is an expert, delete their old profile
        const oldProfileDocRef = doc(firestore, 'userProfiles', authUser.uid);
        await deleteDoc(oldProfileDocRef);
      }

      if (userDocSnapshot.exists()) {
        const userData = userDocSnapshot.data();
        setPhotoURL(userData.photoURL || authUser.photoURL);
        setOriginalPhotoURL(userData.originalPhotoURL || null);
        setFirstName(userData.firstName || (authUser.displayName ? authUser.displayName.split(' ')[0] : ''));
        setLastName(userData.lastName || (authUser.displayName ? authUser.displayName.split(' ')[1] : ''));
        setAddress(userData.address || '');
        setCity(userData.city || '');
        setCounty(userData.county || '');
        setPostcode(userData.postcode || '');
        setMobileNumber(userData.mobileNumber || '');
        setRole(userData.occupation || '');
        setEmail(authUser.email); // Set email from authUser
        setDescription(userData.description || '');
        setSelectedLabel(userData.labels || '');
        setLanguages(userData.languages || ['']); // Set languages state
        setEducation(userData.education || ['']); // Set languages state

        // Fetch employment history data from Firestore
        const employmentHistoryData = userData.employmentHistory || [];
        setEmploymentHistory(employmentHistoryData);
        console.log('Employment History Data:', employmentHistoryData); // Log employmentHistoryData to verify the data structure
      } else {
        setPhotoURL(authUser.photoURL);
        setOriginalPhotoURL(null);
      }

      const userImagesCollectionRef = collection(firestore, `expertApplications/${authUser.uid}/files`);

      const unsubscribeSnapshot = onSnapshot(userImagesCollectionRef, (querySnapshot) => {
        const galleryList = querySnapshot.docs.map((doc) => {
          const data = doc.data();
          const isImage = /\.(jpeg|jpg|gif|png|webp|svg)$/i.test(data.url.split('?')[0]);
          return { id: doc.id, url: data.url, description: data.description || '', title: data.title || '', isImage };
        });

        console.log('Gallery List:', galleryList); // Log galleryList to verify the data structure
        setGallery(galleryList);
      });

      // Fetch links data from Firestore
      const userLinksCollectionRef = collection(firestore, `expertApplications/${authUser.uid}/links`);
      const linksSnapshot = await getDocs(userLinksCollectionRef);

      const linksData = linksSnapshot.docs.map((doc) => {
        const data = doc.data();
        return { id: doc.id, url: data.url, description: data.description };
      });

      setLinks(linksData);

      

      return () => {
        unsubscribeSnapshot();
      };
    } else {
      setAddress('');
      setCity('');
      setCounty('');
      setPostcode('');
      setMobileNumber('');
      setPhotoURL(null);
      setEmail(''); // Clear email
      setRole('');
      setDescription('');
      setLinks([]); // Clear links if not authenticated
    }
  });

  return unsubscribe;
}, []);


const handleFileUpload = async (files, title) => {
  const file = files[0];
  const description = ""; // Replace with actual description or user input
  const fileName = title || file.name; // Use title as filename if provided, otherwise use the original filename
  const userFilesCollectionRef = collection(firestore, `expertApplications/${user.uid}/files`);
  // Check if file size exceeds the limit (1MB)
  if (file.size > 1 * 1024 * 1024) {
    setUploadStatus({ type: 'error', message: 'File size exceeds the limit (1MB).' });
    return; // Abort file upload
  }
  try {
    const scaledFile = file.size > 1 * 1024 * 1024 ? await scaledFile(file) : file;
    const downloadURL = await uploadFile(scaledFile, description, fileName, userFilesCollectionRef); // Pass title and file name
    console.log('Download URL:', downloadURL);
    setUploadStatus({ type: 'success', message: 'File uploaded successfully!' });
  } catch (error) {
    console.error('Error processing file:', error);
    setUploadStatus({ type: 'error', message: 'Error uploading file. Please try again.' });
  }

  // Clear upload status message after 3 seconds
  setTimeout(() => {
    setUploadStatus(null);
  }, 3000);
};

const uploadFile = async (file, description, fileName, collectionRef) => {
  const storageRef = ref(storage, `expertFiles/${user.uid}/${fileName}`);

  try {
    const uploadTask = uploadBytesResumable(storageRef, file);
    const snapshot = await uploadTask;
    const downloadURL = await getDownloadURL(snapshot.ref);
    const uploadedFiles = { id: fileName, url: downloadURL, description, title: '' }; // Set title to empty string
    setGallery(prevGallery => [...prevGallery, uploadedFiles]);
    const docRef = doc(collectionRef, fileName);
    await setDoc(docRef, { url: downloadURL, description, title: '' }); // Set title to empty string

    console.log("Files uploaded and stored successfully");
    return downloadURL;
  } catch (error) {
    console.error('Error uploading Files:', error);
  }
};


const handleRemovePhoto = async (index, event) => {
  try {
    console.log('Gallery before removal:', gallery); // Log entire gallery array
    const files = gallery[index];
    console.log(' object:', files); 
    if (!files) {
      console.error('Error removing photo:  not found in gallery');
      return;
    }
    const fileName = files.id;
    console.log(' name:', fileName); 
    const storageRef = ref(storage, `expertFiles/${user.uid}/${fileName}`);
    await deleteObject(storageRef);
    console.log(' removed successfully from Storage');
    const docPath = `expertApplications/${user.uid}/files/${fileName}`;
    console.log('Firestore document path:', docPath); // Log Firestore document path
    const docRef = doc(firestore, docPath);
    await deleteDoc(docRef);
    console.log('Document removed successfully from Firestore');
    const updatedGallery = gallery.filter((_, i) => i !== index);
    console.log('Updated gallery:', updatedGallery); // Log updated gallery
    setGallery(updatedGallery);
  } catch (error) {
    console.error('Error removing photo:', error);
  }
};

const handleTitleChange = (value, photoId) => {
  const updatedGallery = gallery.map(photo => {
    if (photo.id === photoId) {
      return { ...photo, title: value };
    }
    return photo;
  });
  setGallery(updatedGallery);
};

const handleDescriptionChange = (value, photoId) => {
  const updatedGallery = gallery.map(photo => {
    if (photo.id === photoId) {
      return { ...photo, description: value };
    }
    return photo;
  });
  setGallery(updatedGallery);
};
 
const handleUploadDescription = async (index) => {
  const photo = gallery[index];
  const docRef = doc(collection(firestore, `expertApplications/${user.uid}/files`), photo.id); // Use photo.id (image name) as document ID
  try {
    await Promise.all([
      updateDoc(docRef, { description: photo.description, title: photo.title }), // Update both description and title
    ]);
    console.log("Description and title updated successfully in Firestore");
  } catch (error) {
    console.error('Error updating description and title:', error);
  }
};

