      try {
        setIsLoadingLinks(true);
        const formattedUrl = linkUrl.startsWith('http://') || linkUrl.startsWith('https://') ? linkUrl : `https://${linkUrl}`;
        const userLinksCollectionRef = collection(firestore, `expertApplications/${user.uid}/links`);
        const docRef = await addDoc(userLinksCollectionRef, { url: formattedUrl, description: linkDescription });
        const newLink = { id: docRef.id, url: formattedUrl, description: linkDescription };
        setLinks([...links, newLink]); // Update links state with the new link
        setLinkUrl('');
        setLinkDescription(''); // Clear the description field after adding the link
        setUploadStatusLink({ type: 'success', message: 'Link added successfully!', context: 'links' });
      } catch (error) {
        console.error('Error adding link:', error);
        setUploadStatusLink({ type: 'error', message: 'Error adding link. Please try again.' });
      } finally {
        setIsLoadingLinks(false);
      }
      // Clear upload status message after 3 seconds
      setTimeout(() => {
        setUploadStatusLink(null);
      }, 3000);
