    const handleRemoveLink = async (e, index) => {
      e.preventDefault(); // Prevent default form submission behavior
      try {
        setIsLoadingLinks(true);
        const linkId = links[index].id;
        const linkDocRef = doc(firestore, `expertApplications/${user.uid}/links`, linkId);
        await deleteDoc(linkDocRef);
        const updatedLinks = links.filter((_, i) => i !== index);
        setLinks(updatedLinks);
        setUploadStatusLink({ type: 'success', message: 'Link removed successfully!', context: 'links' });
      } catch (error) {
        console.error('Error removing link:', error);
        setUploadStatusLink({ type: 'error', message: 'Error removing link. Please try again.' });
      } finally {
        setIsLoadingLinks(false);
      }
      // Clear upload status message after 3 seconds
      setTimeout(() => {
        setUploadStatusLink(null);
      }, 3000);
    };
    
    const addLanguage = (event, index, value) => {
      if (event?.key === 'Enter' && event.target.value) {
        setLanguages([...languages, event.target.value]);
        event.target.value = '';
      } else if (index !== undefined && languages[index]) {
        setLanguages([...languages, languages[index]]);
        setLanguages(languages.filter((_, i) => i !== index));
      } else if (value) {
        setLanguages([...languages, value]);
      }
    };
    
    const addEducation = (event, index, value) => {
      if (event?.key === 'Enter' && event.target.value) {
        setEducation([...education, event.target.value]);
        event.target.value = '';
      } else if (index !== undefined && education[index]) {
        setEducation([...education, education[index]]);
        setEducation(education.filter((_, i) => i !== index));
      } else if (value) {
        setEducation([...education, value]);
      }
    };

    const handleRemoveLanguage = (index) => {
      setLanguages(languages.filter((_, i) => i !== index));
    };
    
    const handleRemoveEducation = (index) => {
      setEducation(education.filter((_, i) => i !== index));
    };
    
    
    const handleAddEmployment = () => {
      if (currentEmployer && currentPosition && startDate) {
        const isStartDateBeforeEndDate = !isCurrentlyEmployed ? startDate < endDate : true;
        const isEndDateNotInFuture = !isCurrentlyEmployed ? endDate <= new Date() : true;
    
        if (isStartDateBeforeEndDate && isEndDateNotInFuture) {
          const formattedStartDate = startDate.toLocaleDateString('en-GB', {
            day: '2-digit',
            month: '2-digit',
            year: 'numeric'
          });
    
          const formattedEndDate = isCurrentlyEmployed ? 'Present' : endDate.toLocaleDateString('en-GB', {
            day: '2-digit',
            month: '2-digit',
            year: 'numeric'
          });
    
          setEmploymentHistory([...employmentHistory, { employer: currentEmployer, position: currentPosition, startDate: formattedStartDate, endDate: formattedEndDate }]);
          setCurrentEmployer('');
          setCurrentPosition('');
          setStartDate('');
          setEndDate('');
          setIsCurrentlyEmployed(false);
        } else {
          alert('Please enter valid dates. The start date should be before the end date, and the end date should not be in the future.');
        }
      }
    };
    
    
    const handleRemoveEmployment = (index) => {
      setEmploymentHistory(employmentHistory.filter((_, i) => i !== index));
    };
    
 
    
    
    
    


